import React, { Component } from 'react';
import Header from './components/layout/Header'
import './App.css';
import ShoppingList from './components/layout/ShoppingList'


class App extends Component {
  state = {
    items : [],
    searchResult : {},
    isLoading: true,
    emptyResult: false
  };
  componentDidMount (){
     fetch("https://api.myjson.com/bins/qzuzi")
       .then(res => res.json())
       .then(
         (result) => {
           this.setState({
             items: result,
             isLoading: false
           });
         },        
         (error) => {
           this.setState({
             isLoading: true,
             error
           });
         }
       )
  }

  onAddtocart = () => {
    
  }

searchText = (text) =>{
   const result = this.state.items.filter(obj => Object.keys(obj).some(key => obj[key].toString().toLowerCase().includes(text.toLowerCase())));
  
  this.setState({searchResult: result, emptyResult: Object.entries(result).length === 0 ? true : false   });
}

  render() {
    const {isLoading, items, searchResult, emptyResult} = this.state;
    return (
      <div className="App">
        <div>
          <Header searchText={this.searchText} />
          {emptyResult ? 
            ( 
              <div>No result found</div>) : (
              <ShoppingList isLoading={isLoading} items={Object.entries(searchResult).length !== 0? searchResult : items } onAddtocart={this.onAddtocart}/>
            )
         }
        </div>
      </div>
    );
  }
}

export default App;



